html{font-size:100%;-ms-text-size-adjust:100%;-webkit-text-size-adjust:100%
}
button,html,input,select,textarea{font-family:Helvetica Neue,Arial,PingFang SC,STHeiti,Microsoft YaHei,SimHei,sans-serif
}
body{margin:0
}
a:focus{outline:thin dotted
}
a:active,a:hover{outline:0
}
h1{font-size:2em;margin:.67em 0
}
h2{font-size:1.5em;margin:.83em 0
}
h3{font-size:1.17em;margin:1em 0
}
h4{font-size:1em;margin:1.33em 0
}
h5{font-size:.83em;margin:1.67em 0
}
h6{font-size:.67em;margin:2.33em 0
}
abbr[title]{border-bottom:1px dotted
}
b,strong{font-weight:700
}
blockquote{margin:1em 40px
}
dfn{font-style:italic
}
hr{box-sizing:content-box;height:0
}
p,pre{margin:1em 0
}
code,kbd,pre,samp{font-family:Monaco,Consolas,monospace;font-size:1em
}
pre{white-space:pre;white-space:pre-wrap;word-wrap:break-word
}
q{quotes:none
}
q:after,q:before{content:"";content:none
}
small{font-size:80%
}
sub,sup{font-size:75%;line-height:0;position:relative;vertical-align:baseline
}
sup{top:-.5em
}
sub{bottom:-.25em
}
dl,menu,ol,ul{margin:1em 0
}
dd{margin:0 0 0 40px
}
menu,ol,ul{padding:0 0 0 40px
}
nav ol,nav ul{list-style:none;list-style-image:none
}
img{border:0;-ms-interpolation-mode:bicubic
}
svg:not(:root){overflow:hidden
}
form{margin:0
}
fieldset{border:1px solid silver;margin:0 2px;padding:.35em .625em .75em
}
legend{border:0;padding:0;white-space:normal;*margin-left:-7px
}
button,input,select,textarea{font-size:100%;margin:0;vertical-align:baseline;*vertical-align:middle
}
button,input{line-height:normal
}
button,select{text-transform:none
}
button,html input[type=button],input[type=reset],input[type=submit]{-webkit-appearance:button;cursor:pointer;*overflow:visible
}
button[disabled],html input[disabled]{cursor:default
}
input[type=checkbox],input[type=radio]{box-sizing:border-box;padding:0;*height:13px;*width:13px
}
button::-moz-focus-inner,input::-moz-focus-inner{border:0;padding:0
}
textarea{overflow:auto;vertical-align:top
}
table{border-collapse:collapse;border-spacing:0
}
article,aside,details,figcaption,figure,footer,header,hgroup,main,nav,section,summary{display:block
}
audio,canvas,video{display:inline-block
}
audio:not([controls]){display:none;height:0
}
[hidden]{display:none
}
mark{background:#ff0;color:#666
}
figure{margin:0
}
input[type=search]{-webkit-appearance:textfield;box-sizing:content-box
}
input[type=search]::-webkit-search-cancel-button,input[type=search]::-webkit-search-decoration{-webkit-appearance:none
}
*,:after,:before{box-sizing:border-box
}
html{font-size:14px;font-weight:400
}
@media screen and (-webkit-min-device-pixel-ratio:2),screen and (min-resolution:2dppx){
html{-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale
}
}
h1,h2,h3,h4,h5,h6{font-weight:500
}
button,input,textarea{font:inherit;font-weight:inherit;color:inherit
}
.veui-disabled{cursor:not-allowed!important
}
a{text-decoration:none
}
.veui-button[ui~=link],[ui~=link],a{cursor:pointer;color:#666
}
.veui-button[ui~=link][ui~=primary],[ui~=link][ui~=primary],a[ui~=primary]{color:#3998fc
}
.veui-button[ui~=link].focus-ring,.veui-button[ui~=link]:hover,[ui~=link].focus-ring,[ui~=link]:hover,a.focus-ring,a:hover{color:#3389e3
}
.veui-button[ui~=link]:active,[ui~=link]:active,a:active{color:#2e7aca
}
.veui-button[ui~=link][ui~=aux],[ui~=link][ui~=aux],a[ui~=aux]{color:#999
}
.veui-button[ui~=link][ui~=aux].focus-ring,.veui-button[ui~=link][ui~=aux]:hover,[ui~=link][ui~=aux].focus-ring,[ui~=link][ui~=aux]:hover,a[ui~=aux].focus-ring,a[ui~=aux]:hover{color:#666;text-decoration:underline
}
.veui-button[ui~=link][ui~=aux]:active,[ui~=link][ui~=aux]:active,a[ui~=aux]:active{color:#cc4949
}
.veui-button[ui~=link][ui~=alert],[ui~=link][ui~=alert],a[ui~=alert]{color:#ff5b5b
}
.veui-button[ui~=link][ui~=alert].focus-ring,.veui-button[ui~=link][ui~=alert]:hover,[ui~=link][ui~=alert].focus-ring,[ui~=link][ui~=alert]:hover,a[ui~=alert].focus-ring,a[ui~=alert]:hover{color:#e65252;text-decoration:underline
}
.veui-button[ui~=link][ui~=alert]:active,[ui~=link][ui~=alert]:active,a[ui~=alert]:active{color:#cc4949
}
.veui-button[ui~=link][ui~=success],[ui~=link][ui~=success],a[ui~=success]{color:#5bc49f
}
.veui-button[ui~=link][ui~=success].focus-ring,.veui-button[ui~=link][ui~=success]:hover,[ui~=link][ui~=success].focus-ring,[ui~=link][ui~=success]:hover,a[ui~=success].focus-ring,a[ui~=success]:hover{color:#52b08f;text-decoration:underline
}
.veui-button[ui~=link][ui~=success]:active,[ui~=link][ui~=success]:active,a[ui~=success]:active{color:#499d7f
}
.veui-button[ui~=link][ui~=warning],[ui~=link][ui~=warning],a[ui~=warning]{color:#fe9700
}
.veui-button[ui~=link][ui~=warning].focus-ring,.veui-button[ui~=link][ui~=warning]:hover,[ui~=link][ui~=warning].focus-ring,[ui~=link][ui~=warning]:hover,a[ui~=warning].focus-ring,a[ui~=warning]:hover{color:#e58800;text-decoration:underline
}
.veui-button[ui~=link][ui~=warning]:active,[ui~=link][ui~=warning]:active,a[ui~=warning]:active{color:#cb7900
}
#app{position:fixed;top:0;right:0;bottom:0;left:0
}
#console,#main-nav{font-family:-apple-system,BlinkMacSystemFont,Segoe UI,Helvetica,Arial,sans-serif
}
#main-nav{position:absolute;top:0;bottom:0;left:0;width:240px;border-right:1px solid #eee;font-weight:300
}
#main-nav h1{height:60px;line-height:60px;position:relative;overflow:hidden;border-bottom:1px solid #eee;margin:0;font-size:18px
}
#main-nav h1 a{display:block;width:100%;padding:0 24px;color:#333;text-decoration:none
}
#main-nav h1 .veui-icon{position:absolute;opacity:.05;z-index:-1;top:-25px;right:-5px;transition:transform .6s
}
#main-nav h1:hover .veui-icon{-ms-transform:scale(.8);transform:scale(.8);opacity:.1
}
#main-nav ul{height:calc(100vh - 105px);margin:0;padding:0;overflow:auto
}
#main-nav li a{display:block;padding:.5em 24px;text-decoration:none;color:#999;outline:none;line-height:2
}
#main-nav li a:hover{font-weight:500
}
#main-nav .router-link-active{color:#3998fc;background-color:#f6f9ff;font-weight:500
}
#main-nav footer{position:relative;overflow:hidden;border-top:1px solid #eee;font-size:12px
}
#main-nav footer a{display:block;width:100%;padding-left:24px;line-height:45px;color:#999;text-decoration:none
}
#main-nav footer .veui-icon{position:absolute;opacity:.05;z-index:-1;top:-36px;right:-5px;transition:transform .3s ease-in-out
}
#main-nav footer:hover .veui-icon{-ms-transform:translateY(10px) scale(.3);transform:translateY(10px) scale(.3);opacity:.1
}
main{position:absolute;top:0;right:0;bottom:0;left:240px;overflow:scroll;padding:1em 4em 0;height:calc(100vh - 30px);transition:height .2s
}
main h1{border-bottom:1px solid #eee;margin-bottom:2em;padding-bottom:1em;font-size:18px
}
.console-expanded main{height:calc(100vh - 40vh - 30px)
}
#console{position:fixed;right:0;bottom:0;left:240px
}


