/* Form widths */
.veui-tree,
.veui-tree-item-group {
  list-style: none;
  padding: 0;
  margin: 0;
}
.veui-tree-item {
  height: 36px;
  line-height: 36px;
  *zoom: 1;
}
.veui-tree-item:before,
.veui-tree-item:after {
  display: table;
  content: "";
}
.veui-tree-item:after {
  clear: both;
}
.veui-tree-item-expand-switcher,
.veui-tree-item-label {
  float: left;
}
.veui-tree-item-expand-switcher {
  height: 14px;
  line-height: 14px;
  vertical-align: top;
  margin-right: 4px;
  cursor: pointer;
  margin-top: calc(18px - 0.5em);
}
.veui-tree-item-expand-switcher .veui-icon {
  width: 14px;
  height: 14px;
}
.veui-tree-item-expanded .veui-tree-item-expand-switcher {
  transform: rotateZ(90deg);
}
.veui-tree-item-group {
  display: none;
  padding-left: 18px;
}
.veui-tree-item-expanded + .veui-tree-item-group {
  display: block;
}
.veui-tree-item-label:first-child {
  margin-left: 18px;
}



