
.form-row{margin-bottom:10px;*zoom:1
}
.form-row:after,.form-row:before{display:table;content:""
}
.form-row:after{clear:both
}
.form-key{line-height:42px
}
.form-key,.form-value{float:left
}
.form-value .veui-input,.form-value .veui-textarea{width:280px;margin-right:10px
}
.form-value .veui-textarea{vertical-align:top
}
.form-value .veui-textarea:first-child{height:auto
}
.form-value .veui-textarea:nth-child(2){height:100px
}
.hidden-tips{line-height:42px
}


